  .statistical{
    position: relative;
    width: 100%;
    height: calc(100% - 62px);
    .sta_box{
      position: absolute;
      height: 50%;
      width: 50%;
      padding:15px 20px;
      .con_box{
        height: 100%;
        width: 100%;
        background: #fff;
        border-radius: 10px;
        box-shadow: 0 0 5px 3px rgba(0,0,0,0.1);
        position: relative;
        overflow: hidden;
        .boxBtn{
          position: absolute;
          right: 10px;
          top:10px;
          z-index: 80;
        }
        .bigBox{
          width:calc(100vw - 60px);
          height:100%;
          .smallBox{
            width:50%;
            height:100%;
            float: left;
          }
          .smallBox2{
            width:50%;
            height:100%;
            float: left;
          }
        }
        .smallToBig {
          .smallBox {
            width: 100%;
          }
          .smallBox2 {
            width: 100%;
            height: 50%;
          }
        }
      }
    }
    /* box放大 */
    .activeBox{
      left:0!important;
      top:0!important;
      padding: 15px !important;
      width: 100% !important;
      height: 100%;
      z-index: 99;
      transition: all 0.8s ease;
    }
  }
  #humanPc {
    padding: 15px 15px 0px;
    .ivu-form-item {
      margin: 0;
    }
  }
}
